
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  margin: 0;
  padding: 0;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
a {
  text-decoration: none;
}
button,
button:active,
button:focus {
  border: 0;
  background: #41b883;
  color: #fff;
  cursor: pointer;
  margin: 0 5px;
}
button:disabled,
button:active:disabled,
button:focus:disabled,
button:hover,
button:active:hover,
button:focus:hover {
  background: #35495e;
}
button:disabled,
button:active:disabled,
button:focus:disabled {
  cursor: default;
}
@font-face {
  font-family: vision;
  font-style: normal;
  src: url("assets/fonts/Vision.otf");
}
@font-face {
  font-family: vision;
  font-style: italic;
  src: url("assets/fonts/Vision-LightItalic.otf");
}
@font-face {
  font-family: vision;
  font-weight: 100;
  src: url("assets/fonts/Vision-Light.otf");
}
@font-face {
  font-family: vision;
  font-weight: 700;
  src: url("assets/fonts/Vision-Bold.otf");
}
@font-face {
  font-family: vision;
  font-weight: 900;
  src: url("assets/fonts/Vision-Heavy.otf");
}
a {
  color: #41b883;
}
header {
  padding-top: 2.25em;
}
@media only screen and (max-width: 750px) {
header {
    font-size: 0.7em;
}
}
@media only screen and (max-width: 600px) {
header {
    font-size: 0.6em;
}
}
#logo {
  text-align: center;
  font-family: vision;
  font-size: 2.7em;
  color: #000;
}
#logo em {
  font-family: vision;
  font-style: italic;
  font-size: 0.7em;
  transform: translateX(0.3em);
  display: inline-block;
  margin-right: 0.1em;
}
#logo b {
  font-size: 0.8em;
  font-weight: 700;
  display: inline-block;
  transform: translate(-0.2em, 0.5em);
  letter-spacing: -0.45px;
}
nav {
  font-family: vision;
  list-style: none;
  position: absolute;
  top: 10em;
  right: -0.4em;
  transform: rotateZ(270deg);
}
nav li {
  display: inline-block;
}
nav li:last-child {
  margin-left: 1em;
}
nav a {
  color: #000;
  letter-spacing: 1.11px;
  position: relative;
}
nav a:after {
  content: '';
  position: absolute;
  width: 0%;
  height: 1px;
  left: 0;
  bottom: -1px;
  background-color: #000;
  transition: width 300ms ease-in-out;
}
nav a:hover::after {
  width: 100%;
}
nav a.router-link-exact-active:after {
  width: 100%;
}


