
#face {
  position: relative;
  height: 100%;
}
#face #eyes {
  margin: 0 auto;
}
#face #eyes .eye,
#face #mouth .mouth__main {
  fill: #192534;
}
#face #blushes .blush,
#face #mouth .mouth__tang {
  fill: #ec497b;
}
#face #mouth .mouth__tang {
  transform: translateX(0.5px);
  opacity: 0;
}
#face #mouth .mouth__main {
  transform-origin: 50% 50%;
}


