
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  margin: 0;
  padding: 0;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
a {
  text-decoration: none;
}
button,
button:active,
button:focus {
  border: 0;
  background: #41b883;
  color: #fff;
  cursor: pointer;
  margin: 0 5px;
}
button:disabled,
button:active:disabled,
button:focus:disabled,
button:hover,
button:active:hover,
button:focus:hover {
  background: #35495e;
}
button:disabled,
button:active:disabled,
button:focus:disabled {
  cursor: default;
}
@font-face {
  font-family: vision;
  font-style: normal;
  src: url("assets/fonts/Vision.otf");
}
@font-face {
  font-family: vision;
  font-style: italic;
  src: url("assets/fonts/Vision-LightItalic.otf");
}
@font-face {
  font-family: vision;
  font-weight: 100;
  src: url("assets/fonts/Vision-Light.otf");
}
@font-face {
  font-family: vision;
  font-weight: 700;
  src: url("assets/fonts/Vision-Bold.otf");
}
@font-face {
  font-family: vision;
  font-weight: 900;
  src: url("assets/fonts/Vision-Heavy.otf");
}
a {
  color: #41b883;
}
body {
  overflow: hidden;
}
@media only screen and (max-width: 750px) {
body {
    font-size: 0.9em;
}
}
@media only screen and (max-width: 600px) {
body {
    font-size: 0.3em;
}
}
#app {
  text-align: center;
}
#border {
  position: fixed;
  z-index: -1;
  border: 12px solid transparent;
  border-right-width: 14px;
  border-left-width: 14px;
  border-image: url("assets/img/border.svg") 1% stretch;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.icon-logo {
  margin-bottom: 2.5rem;
}


